.check-page {
    max-width: 600px;
    margin: 40px auto;
    padding: 30px;
    font-family: 'Space Mono', monospace;
    background: #f6f5f8;
    border: 4px solid #000;
    border-radius: 6px;
    box-shadow: 6px 6px 0 #404040;
  }
  
  .check-form {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 15px;
    margin-top: 20px;
  }
  
  .check-form input {
    padding: 12px;
    font-size: 16px;
    width: 280px;
    border: 2px solid #000;
    border-radius: 6px;
    box-shadow: 3px 3px 0 #404040;
  }
  
  .check-form button {
    background-color: #a47beb;
    border: 2px solid #000;
    padding: 10px 20px;
    font-weight: bold;
    cursor: pointer;
    font-size: 16px;
    border-radius: 6px;
    box-shadow: 3px 3px 0 #404040;
    transition: transform 0.2s ease, box-shadow 0.2s ease;
  }
  
  .check-form button:hover {
    background-color: #b89bf7;
    transform: translate(-2px, -2px);
    box-shadow: 5px 5px 0 #404040;
  }
  
  .check-result {
    margin-top: 30px;
    background: #f6f5f8;
    border: 3px solid #000;
    box-shadow: 6px 6px 0 #404040;
    padding: 20px;
    border-radius: 6px;
    max-width: 400px;
    margin-left: auto;
    margin-right: auto;
  }
  
  .suggestion {
    margin-top: 10px;
    color: #dc2626;
    font-weight: bold;
  }
  