.main {
    max-width: 700px;
    margin: 40px auto;
    padding: 30px;
    font-family: 'Space Mono', monospace;
    background: #f6f5f8;
    border: 4px solid #000;
    border-radius: 8px;
    box-shadow: 8px 8px 0 #404040;
  }
  
  .main-title {
    font-size: 40px;
    text-align: center;
    margin: 0 auto 20px;
    background: #2dd1a3;
    border: 4px solid #000;
    padding: 12px;
    box-shadow: 6px 6px 0 #404040;
    border-radius: 8px;
    transition: transform 0.2s ease;
    display: flex;
    justify-content: center;
    width: fit-content;
  }
  
 
  .main-subtitle {
    font-size: 20px;
    text-align: center;
    margin-bottom: 30px;
    color: #000;
  }
  
  .donate {
    margin-bottom: 30px;
    padding: 25px;
    border: 4px solid #000;
    background: #fafafa;
    box-shadow: 5px 5px 0 #404040;
    border-radius: 8px;
    transition: transform 0.2s ease;
  }
  
  .donate h2 {
    font-size: 26px;
    margin-bottom: 12px;
    font-weight: 700;
    color: #000;
  }
  
  .donate p {
    font-size: 16px;
    line-height: 1.6;
    margin-top: 5px;
    color: #010101;
  }
  
  .footer {
    border-top: 4px solid #000;
    padding-top: 15px;
    text-align: center;
    font-size: 14px;
    color: #010101;
    margin-top: 30px;
  }
  
  .footer a {
    color: #010101;
    text-decoration: none;
    font-weight: bold;
    margin: 0 8px;
  }
  
  .footer a:hover {
    text-decoration: underline;
    color: #2dd1a3;
  }