.nav {
    display: flex;
    justify-content: center;
    gap: 20px;
    margin-bottom: 30px;
  }
  
  .nav-link {
    font-family: 'Space Mono', monospace;
    font-weight: bold;
    font-size: 16px;
    padding: 10px 15px;
    background: #2dd1a3;
    border: 2px solid #000;
    border-radius: 6px;
    box-shadow: 3px 3px 0 #404040;
    text-decoration: none;
    color: #000;
    transition: transform 0.2s ease, box-shadow 0.2s ease;
  }
  
  .nav-link:hover {
    transform: translate(-2px, -2px);
    box-shadow: 5px 5px 0 #404040;
    background: #4de7bc;
  }
  .navbar {
    display: flex;
    justify-content: center;
    gap: 20px;
    margin-bottom: 30px;
  }
  
  
  .nav-link.active {
    border-bottom: 2px solid #000;
    background-color: #a47beb;
    color: #000;
  }
  